@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:ital,wght@0,600;1,400&display=swap");
.text-violet {
  color: #674baf;
}

.bg-violet {
  background-color: #674baf;
}

.text-soft-magenta {
  color: #e882e8;
}

.bg-soft-magenta {
  background-color: #e882e8;
}

html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  bottom: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  height: 100%;
  font-family: "Poppins", sans-serif;
  position: relative;
}

.card {
  background-color: salmon;
}

.hero {
  color: aqua;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}